<template>
  <div class="text-end">
    <button type="button" class="btn btn-primary"
    v-on:click="openModel(true)"></button>
  </div>
  <table class="table mt-4">
    <thead>
      <tr>
        <th width="120"></th>
        <th></th>
        <th width="120"></th>
        <th width="120"></th>
        <th width="100"></th>
        <th width="200"></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in products" v-bind:key="'key'+index">
        <td>{{ item.category }}</td>
        <td>{{ item.title }}</td>
        <td class="text-right">
          {{ item.origin_price }}
        </td>
        <td class="text-right">
          {{ item.price }}
        </td>
        <td>
          <span class="text-success" v-if="item.is_enabled"></span>
          <span class="text-muted" v-else></span>
        </td>
        <td>
          <div class="btn-group">
            <button class="btn btn-outline-primary btn-sm" v-on:click="openModel(false,item)"></button>
            <button class="btn btn-outline-danger btn-sm"></button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <product-model ref="ProductModel" v-bind:product="tempProduct" v-on:update-product="updateProduct"></product-model>
</template>

<script>
import ProductModel from '../components/ProductModel.vue'

export default {
  data () {
    return {
      // 
      products: [],
      // 
      pagination: {},
      // 
      tempProduct: {},
      // 
      isNew: true
    }
  },
  components: {
    ProductModel
  },
  methods: {
    // 
    getProducts () {
      const api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/products`
      this.axios.get(api)
        .then((response) => {
          if (response.data.success) {
            console.log(response)
            this.products = response.data.products
            this.pagination = response.data.pagination
          }
        })
    },
    // 
    openModel (isNew, item) {
      if (isNew) {
        this.tempProduct = {}
      } else {
        this.tempProduct = { ...item }
      }
      this.isNew = isNew
      const Model = this.$refs.ProductModel
      Model.showModel()
    },
    updateProduct (item) {
      this.tempProduct = item
      let api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/product`
      let method = 'post'
      if (!this.isNew) {
        api = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_API}/admin/product/${item.id}`
        method = 'put'
      }
      this.axios[method](api, { data: this.tempProduct })
        .then((response) => {
          console.log(response)
          if (response.data.success) {
            this.$refs.ProductModel.hideModel()
            this.getProducts()
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>
